	module.exports = " <div class=theater _v-c8ff2040=\"\"> <h1 class=title _v-c8ff2040=\"\">Theater</h1> <div class=skills _v-c8ff2040=\"\"> <div class=skill _v-c8ff2040=\"\"> <dial class=dial :percent=93 _v-c8ff2040=\"\"></dial> <div class=name _v-c8ff2040=\"\">Sound Mixing</div> </div> <div class=skill _v-c8ff2040=\"\"> <dial class=dial :percent=82 _v-c8ff2040=\"\"></dial> <div class=name _v-c8ff2040=\"\">Sound Design</div> </div> <div class=skill _v-c8ff2040=\"\"> <dial class=dial :percent=72 _v-c8ff2040=\"\"></dial> <div class=name _v-c8ff2040=\"\">Lighting Design</div> </div> <div class=skill _v-c8ff2040=\"\"> <dial class=dial :percent=100 _v-c8ff2040=\"\"></dial> <div class=name _v-c8ff2040=\"\">Cable Management</div> </div> <div class=skill _v-c8ff2040=\"\"> <dial class=dial :percent=70 _v-c8ff2040=\"\"></dial> <div class=name _v-c8ff2040=\"\">Set Construction</div> </div> </div> <p class=brief _v-c8ff2040=\"\"> Theater has a special place in my heart. Originally supposed to be a one-time-thing to gather credits for graduation, I ended up participating in Laguna Hills High School's (LHHS) drama program all four years of my schooling. I fell in love with the world of live theater and saw first-hand just how magical it can be. I love that I get to create a whole world for the audience to experience. </p> <p class=brief _v-c8ff2040=\"\"> After two years at LHHS, I took on the role of sound lead. After doing so, I became the first cappie eligible sound tech at LHHS because I performed all of the equalization, mic'ing, demic'ing, mixing, and sound design myself without the help of a non-student member. As such, I was pushed to my limit with every show only to exceed it. With each performance under my belt, the quality of my work improves and is apparent to the audience (who can enjoy the show without a worry). </p> <div class=links _v-c8ff2040=\"\"> <h1 _v-c8ff2040=\"\">Accolades</h1> <h4 _v-c8ff2040=\"\">more to come</h4> <div class=accolades _v-c8ff2040=\"\"> <span class=accolade _v-c8ff2040=\"\"> <span class=year _v-c8ff2040=\"\">2015</span> <span class=name _v-c8ff2040=\"\">Cappie - Best Sound In A Musical</span> </span> </div> </div> <div class=links _v-c8ff2040=\"\"> <h1 _v-c8ff2040=\"\">Previous Work</h1> <h4 _v-c8ff2040=\"\">where i've been</h4> <h2 _v-c8ff2040=\"\"><a href=http://www.creativekidsplayhouse.com/ target=_blank _v-c8ff2040=\"\">laguna_hills_high_school - Sound Lead (4 years)</a></h2> <h2 _v-c8ff2040=\"\"><a href=http://siteline2.vendini.com/site/lhhsdrama.com target=_blank _v-c8ff2040=\"\">creative_kids_playhouse - Sounds &amp; Lights (2 years)</a></h2> </div> </div> ";

